
.forcegraph {
  width: 100%;
  height: 600px;
  white-space: pre-wrap;
  background-color: #FAFAFA;
  border: 1px solid #E8E8E8 !important;
}
rect {
  cursor: -webkit-grab;
  cursor: grab;
}
.links line {
  fill: none;
  stroke: #aaa;
  stroke-width: 1px;
}
.nodes circle {
  pointer-events: all;
  cursor: move;
}
line.highlighted {
  stroke: red;
  stroke-dasharray: 2;
}
circle.highlighted {
  stroke: red;
  stroke-width: 2;
}
text.highlighted {
  opacity: 1 !important;
  font-weight: bold !important;
}
.labels text {
  opacity: 0.3;
  font-size: 6px;
  pointer-events: none;
}
.linklabels text {
  opacity: 0.3;
  font-size: 6px;
  pointer-events: none;
}



