
.colorbox {
  float: left;
  width: 10px;
  height: 10px;
  margin: 6px;
  border: 1px solid rgba(0, 0, 0, .2);
}
.allcolor {
  background: black;
}
.pendingcolor {
  background: #7293cb;
}
.startedcolor {
  background: #90679d;
}
.failurecolor {
  background: #e1974c;
}
.sucpiciouscolor {
  background: #eec49b;
}
.successfulcolor {
  background: #84ba5b;
}
.clearcolor {
  background: #c9e1b7;
}
.maliciouscolor {
  background: #d35e60;
}



