
.label {
  font-size: 11px;
  padding: .3em .4em .4em;
  margin: 0 .1em;
}
.label a {
  color: #fff;
}
.label a {
  margin: 0 .5em 0 .3em;
  opacity: 1.0;
}
.urlcell {
  max-width: 200px;
  max-height: 200px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.namecell {
  max-width: 50px;
  max-height: 50px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.submittercell {
  max-width: 50px;
  max-height: 50px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.statuscell {
  max-width: 100px;
}
.classificationcell {
  max-width: 100px;
}
.hoverable:hover {
  white-space: normal;
  position: absolute;
  max-width: none;
  background: #fff;
  z-index: 100;
  overflow: visible;
  border-top: 1px solid #ddd !important;
}



